html {
  font-family: "Muller", "Helvetica Neue", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  min-width: 375px;
  -webkit-font-smoothing: antialiased; }

.frame {
  overflow: hidden;
  display: block; }

@media (min-width: 768px) {
  html {
    font-size: 24px; } }

.main-title {
  font-size: 2.827rem;
  font-weight: 800;
  text-transform: uppercase;
  line-height: 1.4;
  color: #fff;
  letter-spacing: .05em;
  margin: 1em 0 .5em;
  max-width: 500px; }
  .main-title .main-title__desc {
    display: block;
    font-size: 1.312rem;
    text-transform: none;
    line-height: 1.5;
    font-weight: 400;
    letter-spacing: normal; }

.title {
  font-size: 1.999rem;
  line-height: 1.4;
  margin: 1em 0 .6em; }

@media (min-width: 768px) {
  .main-title .main-title__desc {
    font-size: 24px; } }

.page-header {
  min-height: 70vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  box-sizing: border-box;
  margin-bottom: 10vh; }
  .page-header .m18-logo {
    position: absolute;
    margin: 40px 0 0 6.5vw;
    width: 85px;
    height: 51px;
    cursor: pointer;
    transition: transform .3s ease-in; }
    .page-header .m18-logo:hover {
      transform: scale(1.1, 1.1); }

.container {
  display: block;
  box-sizing: border-box;
  min-width: 375px;
  width: 100%;
  padding: 0 6.5vw;
  position: relative; }

@keyframes enterDown {
  from {
    transform: translateY(-100%); }
  to {
    transform: translateY(0); } }

@keyframes slideInDown {
  from {
    transform: translateY(-20px); }
  to {
    transform: translateY(0); } }

@keyframes headerBgIn {
  from {
    margin-top: -10vh;
    background-position: center 10vh; }
  to {
    margin-top: 0;
    background-position: center center; } }

.case {
  width: 100%;
  box-sizing: border-box; }
  .case__img img {
    max-width: 100%;
    height: auto;
    display: block; }

.fix-remont .page-header {
  background-image: url(../img/cover--sm.jpg);
  animation: headerBgIn .6s ease-out 1 none;
  position: relative; }
  .fix-remont .page-header .promo.cards {
    display: none; }
  .fix-remont .page-header .main-title {
    margin-top: 24vh;
    animation: slideInDown .8s ease-out 1 both;
    margin-bottom: 8vh; }
  .fix-remont .page-header .frame > span {
    position: relative;
    display: block;
    animation: enterDown .8s ease-out 1 both; }
    .fix-remont .page-header .frame > span:nth-child(1) {
      animation-delay: .2s; }
    .fix-remont .page-header .frame > span:nth-child(2) {
      animation-delay: .3s; }

@media (min-width: 768px) {
  .fix-remont .page-header {
    min-height: 85vh; }
    .fix-remont .page-header .container {
      position: static; }
    .fix-remont .page-header .main-title {
      margin-top: 20vh; }
    .fix-remont .page-header .promo.cards {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: nowrap;
      width: 200%;
      position: absolute;
      bottom: -60px; }
    .fix-remont .page-header .cards__item {
      border-radius: 5px;
      margin-right: 3vw;
      overflow: hidden;
      box-shadow: 0 20px 60px -10px rgba(0, 0, 0, 0.4);
      position: relative; }
      .fix-remont .page-header .cards__item::after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 40px;
        bottom: 60px;
        background: linear-gradient(to bottom, transparent 10%, rgba(0, 0, 0, 0.1) 100%); }
    .fix-remont .page-header .cards__item img {
      max-width: 100%;
      height: auto;
      display: block; } }
